<template>
    <div class="vue-description">
      <p class="vue-description-remark" :class="remarkClass" >
        {{desc}}
      </p>
      <div class="vue-description-more " v-show="hasMore" v-on:click="toggleMore()"></div>
    </div>
</template>

<script>

  export default {
    data(){
      return {
        remarkClass: '',
        hasMore: false,
      }
    },
    props: {
      className: {
        type: String
      },
      line: {
        type: String,
        default: 3
      },
      desc: {
        type: String,
        default: ''
      }
    },
    methods: {
      toggleMore(){
        this.hasMore = false;
      }
    },
    watch: {
      'hasMore': function (inItem) {
        this.hasMore ? this.remarkClass = 'lc-' + this.line + ' ' + this.className : this.remarkClass = this.className;
      }
    },
    mounted(){
      if (this.desc.length > (parseInt(this.line) * 28) || this.desc.split(`\n`) > parseInt(this.line)) {
        this.hasMore = true;
      }
    }
  }
</script>
<style rel="stylesheet/scss" lang="scss">

  .vue-description {
    &-remark {
      word-break: break-all;
      /* autoprefixer: off */
      -webkit-box-orient: vertical;
      /* autoprefixer: on */
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      font-size: .28rem;
      line-height: .42rem;
      color: #333;
      transition: all 1s;
    }
    &-more {
      height: .6rem;
      text-align: center;
      line-height: .6rem;
      &:after {
        content: '';
        width: .22rem;
        height: .22rem;
        background-image: url("data:image/png;base64,");
        background-repeat: no-repeat;
        background-size: contain;
        display: inline-block;
      }

    }
  }

  .lc-1 {
    -webkit-line-clamp: 1;
  }

  .lc-2 {
    -webkit-line-clamp: 2;
  }

  .lc-3 {
    -webkit-line-clamp: 3;
  }

  .lc-4 {
    -webkit-line-clamp: 4;
  }

  .lc-5 {
    -webkit-line-clamp: 5;
  }

  .lc-6 {
    -webkit-line-clamp: 6;
  }
</style>



